/* @after Calendar/widget/CalendarRow.css */

:root, :host {
    /**
     * Calendar row border color
     */
    --b-day-resource-calendar-row-border-color : var(--b-calendar-border-color);
}

.b-internal {
    --bi-visible-resource-count : null;
}

div.b-day-resource-calendar-row {

    &.b-hide-all-day-header .b-day-resource-calendar-row-column,
    .b-day-resource-calendar-row-column {
        position  : relative;
        display   : flex;
        flex-flow : row;
        flex      : 1 0 auto;

        &:not(:last-child) {
            border-inline-end : var(--b-month-view-cell-border-width) solid var(--b-day-resource-calendar-row-border-color);
        }

        .b-resource-calendar-row-column-resource-cell {
            flex      : 1 1 100%;
            min-width : var(--bi-min-resource-width);
            display   : flex;
            flex-flow : column nowrap;

            &:not(:last-child) {
                border-inline-end : var(--b-month-view-cell-border-width) solid var(--b-day-view-border-color);
            }
        }

        .b-cal-event-bar-container {
            position : relative;
        }
    }

    &.b-hide-all-day-header .b-cal-cell-header,
    .b-cal-cell-header {
        align-items           : stretch;
        padding-bottom        : 0;

        /* It's widthed by either the minDayWidth or the minResourceWidths of the resource cells. */
        flex                  : 1 0 var(--bi-min-day-width, auto);

        display               : grid;
        grid-template-columns : repeat(max(var(--bi-visible-resource-count), 1), 1fr);

        /* Override settings from base CalendarRow */
        contain               : unset;
        width                 : auto;

        &:not(:last-child) {
            border-inline-end : var(--b-month-view-cell-border-width) solid var(--b-day-resource-calendar-row-border-color);
        }
    }

    .b-dayname-date {
        grid-row     : 1;
        grid-column  : 1/-1;
        display      : flex;
        flex-flow    : column nowrap;
        align-items  : center;
        padding      : .5em 0;

        max-width    : var(--bi-min-resource-width);

        /* This element must hang out in the center. */
        /* Owning grid can't justify-items : center because it has to flex three cells in the second row. */
        justify-self : center;
    }

    &.b-compact-header {
        .b-cal-cell-header {
            gap     : 0;
            padding : 0;
        }
        .b-dayname-date {
            flex-flow : row nowrap;
            gap       : 0.3em;
        }
    }

    &:not(.b-compact-header) {
        .b-day-name, .b-day-date {
            flex            : 1;
            display         : flex;
            justify-content : center;
        }
    }

    .b-day-resource-calendar-row-resource-header {
        grid-row        : 2/2;
        display         : flex;
        gap             : .8em;
        justify-content : center;
        align-items     : center;
        padding-block   : 1em;
        padding-inline  : 0.5em;
        overflow        : hidden;
        /*noinspection CssInvalidPropertyValue*/
        contain         : inline-size layout;

        border-top      : var(--b-month-view-cell-border-width) solid var(--b-calendar-border-color);
        min-width       : var(--bi-min-resource-width);

        /* Hide resources row if there are no visible resources */
        /* It contributes width to the layout, so it must be zero height, not display:none */
        .b-no-visible-resources & {
            max-height : 0;
            padding    : 0;
            border     : 0 none;
        }

        &.b-has-info {
            display               : grid;
            grid-template-areas   :
                "avatar name"
                "info info";
            grid-template-columns : auto 1fr;
        }

        .b-resource-avatar {
            grid-area    : avatar;
            border-color : var(--b-calendar-border-color);
        }

        &.b-avatar-after {
            .b-resource-avatar {
                order : 1;
            }
        }

        &:not(:last-child) {
            border-inline-end : var(--b-month-view-cell-border-width) solid var(--b-calendar-border-color);
        }

        .b-day-resource-calendar-row-resource-info {
            grid-area : info;
        }
    }

    /* Hide the resource avatar is the header cell gets narrow */
    &.b-narrow-resource-header {
        .b-day-resource-calendar-row-resource-header {
            .b-resource-avatar {
                display : none;
            }
        }

        /* Alternatively, may be configured to hid the resource *name* */
        &.b-hide-resource-name-when-narrow {
            .b-day-resource-calendar-row-resource-header {
                .b-resource-avatar {
                    display : flex;
                }

                .b-day-resource-calendar-row-resource-name {
                    display : none;
                }
            }
        }
    }

    .b-day-resource-calendar-row-resource-name {
        white-space   : nowrap;
        text-overflow : ellipsis;
        overflow      : hidden;
        line-height   : var(--b-avatar-size);
        grid-area     : name;
    }

    /* The future bars which, in a continuous CalendarRow are opacity:0 and inactivated */
    /* because the main bar starting at the startDate covers it are active in this view. */
    /* Each occurrence in each column must be interactive. This rule must not be more specific */
    /* because when dragging (b-draggable-started added to view), the events are inactivated */
    /* with a rule that applies pointer-events:none */
    .b-cal-event-wrap {
        max-width      : 100%;
        opacity        : 1;
        pointer-events : all;
    }
}
