:root, :host {
    /**
     * Event buffer background
     */
    --b-calendar-event-buffer-background   : var(--b-neutral-100);

    /**
     * Event buffer border color
     */
    --b-calendar-event-buffer-border-color : var(--b-border-5);

    /**
     * Event buffer text color
     */
    --b-calendar-event-buffer-color        : var(--b-text-2);
}

.b-day-view {
    --bi-preamble-line-color        : var(--b-calendar-event-buffer-border-color);
    /* Create the "T" background images for the preamble and postamble elements */
    --bi-event-preamble-background  : linear-gradient(90deg, transparent, transparent calc(50% - 0.5px), var(--bi-preamble-line-color) calc(50% - 0.5px), var(--bi-preamble-line-color) calc(50% + 0.5px), transparent calc(50% + 0.5px), transparent 100%), linear-gradient(180deg, var(--bi-preamble-line-color) 1px, transparent 1px);
    --bi-event-postamble-background : linear-gradient(90deg, transparent, transparent calc(50% - 0.5px), var(--bi-preamble-line-color) calc(50% - 0.5px), var(--bi-preamble-line-color) calc(50% + 0.5px), transparent calc(50% + 0.5px), transparent 100%), linear-gradient(0deg, var(--bi-preamble-line-color) 1px, transparent 1px);
}

.b-cal-event-buffer {
    position              : absolute;
    width                 : 100%;
    display               : grid;
    grid-template-rows    : 0;
    grid-template-columns : auto auto;
    place-items           : center;
    place-content         : center;
    background-size       : 1em 100%;
    background-repeat     : no-repeat;
    background-position   : 50% 0%;
    color                 : #aaa;
    font-size             : 10px;
    min-height            : 1.5em;
    pointer-events        : none;

    .b-travel-time-label[data-btip] {
        pointer-events : all;
    }

    i {
        margin-inline-end : 0.5em;
    }
}

.b-cal-preamble {
    bottom           : 100%;

    background-image : var(--bi-event-preamble-background);
}

.b-cal-postamble {
    top              : 100%;

    background-image : var(--bi-event-postamble-background);
}

.b-travel-time-label {
    border-radius : .7em;
    padding       : .1em .7em;
    text-align    : center;

    background    : var(--b-calendar-event-buffer-background);
    color         : var(--b-calendar-event-buffer-color);
    border        : 1px solid var(--b-calendar-event-buffer-border-color);
}
