.b-grid-to-cal-drag-proxy {
    position       : absolute;
    z-index        : 10;
    pointer-events : none;
    opacity        : 0.9;
    width          : 15em;
    height         : 25px;
    align-items    : center;
    display        : flex;
    overflow       : hidden;
    text-overflow  : ellipsis;

    background     : var(--b-calendar-solid-bar-background);
    border-radius  : var(--b-calendar-event-border-radius);
    color          : var(--b-calendar-solid-bar-color);
    font-size      : var(--b-month-view-event-bar-font-size);
    padding        : 0 var(--b-calendar-event-bar-padding-start);

    /* !* If there's an icon, space it from any text *! */
    /* .b-fa { */
    /*     font-size : $cal-event-icon-font-size; */
    /*     &:first-child { */
    /*         margin-inline-end : $cal-event-bar-gap; */
    /*     } */

    /*     &:not(:first-child) { */
    /*         margin-inline-start : $cal-event-bar-gap; */
    /*     } */
    /* } */
}

.b-grid-row.b-drop-above:before {
    content          : "";
    position         : absolute;
    height           : 1px;
    width            : 100%;
    z-index          : 1;

    background-color : var(--b-row-reorder-indicator-color, var(--b-secondary));
}

:is(.b-grid-row, .b-grid-header-container).b-drop-below:before {
    content          : "";
    position         : absolute;
    top              : calc(100% - 1px);
    height           : 1px;
    width            : 100%;
    background-color : red;
    z-index          : 1;
}
