/* @after Calendar/widget/YearView.css */
:root, :host {
    /**
     * Month background color
     */
    --b-month-grid-month-background-color               : var(--b-neutral-98);

    /**
     * Event container background color (within each month)
     */
    --b-month-grid-event-container-background-color     : transparent;

    /**
     * Event group background color. This is used when grouping events in the month grid.
     */
    --b-month-grid-event-group-background-color         : var(--b-neutral-100);

    /**
     * Event group border color. This is used when grouping events in the month grid.
     */
    --b-month-grid-event-group-border-color             : var(--b-calendar-border-color);

    /**
     * Event group border radius. This is used when grouping events in the month grid.
     */
    --b-month-grid-event-group-border-radius            : var(--b-widget-border-radius);

    /**
     * Event group expand icon color. This is used when grouping events in the month grid.
     * The icon is used to expand/collapse the event group.
     */
    --b-month-grid-event-group-expand-icon-color        : var(--b-text-4);

    /**
     * Event group active expand icon color. This is used when grouping events in the month grid.
     */
    --b-month-grid-event-group-active-expand-icon-color : var(--b-text-5);

    /**
     * Month header background
     */
    --b-month-grid-month-header-background       : transparent;

    /**
     * Month header hover background
     */
    --b-month-grid-month-header-hover-background : transparent;

    /**
     * Month header text color
     */
    --b-month-grid-month-header-color            : var(--b-text-1);
}

.b-internal {
    --bi-min-month-height : null;
}

.b-month-grid {
    --b-calendar-view-padding-top : 0;

    &.b-responsive-small {
        font-size : 0.7em;
    }

    .b-hide-event-scroll {
        .b-month-grid-content-wrap {

            /* Gradient overlays for overflow at top and bottom of event container */
            /* These are used to indicate that there are more events above or below the visible area */
            /* They are positioned absolutely within the content wrap */
            /* and use a linear gradient to fade from the background color to transparent */
            /* This allows us to show a visual cue that there are more events to scroll to. */
            &::before, &::after {
                pointer-events : none;
                transition     : opacity 0.3s ease;
                opacity        : 0;
                content        : '';
                position       : absolute;
                left           : 0;
                right          : 0;
                z-index        : 12;
                height         : 20px; /* Height of the gradient */
            }

            &.b-cal-event-container-overflow-top {
                &::before {
                    opacity    : 1;
                    top        : 0;

                    background : linear-gradient(to bottom,
                    var(--b-month-grid-month-background-color) 0%, /* Start with background-color at the top */ transparent 100% /* Fade to transparent by 20px from the top */
                    );
                }
            }

            &.b-cal-event-container-overflow-bottom {
                &::after {
                    opacity    : 1;
                    bottom     : 0;

                    background : linear-gradient(to bottom,
                    transparent 0%, /* Start transparent */ var(--b-month-grid-month-background-color) 100% /* Fade up to background-color at the bottom */
                    );
                }
            }
        }

        /* If the platform *has* space-taking scrollbars, hide them */
        /* If they are floating scrollbars, they should not be hidden. */
        &.b-has-scrollbars {
            .b-cal-event-bar-container {
                scrollbar-width : none;
            }
        }
    }

    .b-month-grid-content {
        overflow : auto;
    }
}

.b-month-grid-body-wrap {
    container : month-grid / inline-size;
}

.b-month-grid-content {
    display               : grid;
    gap                   : var(--bi-view-gap, 0);
    padding               : var(--bi-view-gap, 0);
    align-items           : stretch;

    grid-template-columns : repeat(var(--bi-column-count), 1fr);
    grid-template-rows    : repeat(var(--bi-row-count), 1fr);

    /* If view width doesn't reach any of the min-width breakpoints, default to 1 column and 12 rows */
    --bi-column-count     : 1;
    --bi-row-count        : 12;

    @container month-grid (min-width: 380px) {
        --bi-column-count : 2;
        --bi-row-count    : 6;
    }
    @container month-grid (min-width: 570px) {
        --bi-column-count : 3;
        --bi-row-count    : 4;
    }
    @container month-grid (min-width: 760px) {
        --bi-column-count : 4;
        --bi-row-count    : 3;
    }
    @container month-grid (min-width: 1140px) {
        --bi-column-count : 6;
        --bi-row-count    : 2;
    }

    .b-has-view-gap & {
        --b-calendar-view-padding-top : var(--bi-view-gap);
    }

    .b-month-grid-month {
        display          : flex;
        flex-direction   : column;
        overflow         : hidden;
        border-style     : solid;
        border-color     : var(--b-calendar-border-color);

        border-width     : 0 0 1px 1px;
        min-height       : var(--bi-min-month-height);
        background-color : var(--b-month-grid-month-background-color);

        .b-has-view-gap & {
            border-width  : 1px;
            border-radius : var(--b-widget-border-radius);
        }
    }

    .b-month-grid-month-name {
        display         : flex;
        padding         : 1em 1em 0.5em 1em;
        font-size       : 1em;
        font-weight     : 500;
        max-width       : unset;
        margin          : 0;
        justify-content : space-between;
        cursor          : pointer;
        align-items     : baseline;

        background      : var(--b-month-grid-month-header-background);
        color           : var(--b-month-grid-month-header-color);

        &:hover {
            background : var(--b-month-grid-month-header-hover-background);
        }

        &:focus-visible {
            outline-offset : -1px;
        }
    }

    .b-month-grid-month-header-event-count {
        font-size : 0.8em;
    }

    .b-month-grid-content-wrap {
        flex-grow : 1;
        display   : flex;
        position  : relative;
        overflow  : hidden;

        margin    : var(--bi-event-spacing);
    }

    .b-month-grid-month .b-cal-event-bar-container {
        overflow-x       : visible;
        overflow-y       : auto;

        gap              : var(--bi-event-spacing);
        background-color : var(--b-month-grid-event-container-background-color);

        /* For when grouping is used in the month grid. */
        /* An event bar group shows information about a group of events. */
        .b-cal-event-bar-group {
            display                : flex;

            background-color       : var(--b-month-grid-event-group-background-color);
            border                 : 1px solid var(--b-month-grid-event-group-border-color);
            border-radius          : var(--b-month-grid-event-group-border-radius);
            --bi-expand-icon-color : var(--b-month-grid-event-group-expand-icon-color);

            &.b-active {
                --bi-expand-icon-color : var(--b-month-grid-event-group-active-expand-icon-color);
            }
        }

        .b-cal-event-bar-count {
            margin-inline-start : auto;
        }

        .b-cal-event-body, .b-cal-event-desc {
            justify-content : space-between;
            display         : flex;
            flex-basis      : 100%;
        }

        .b-icon-group-expand {
            &::before {
                transition : rotate 0.3s ease;
            }

            &.b-rtl {
                &::before {
                    rotate : -180deg;
                }
            }
        }

        .b-icon-group-expand {
            color : var(--bi-expand-icon-color);
        }

        &:hover {
            color : currentColor;
        }

        &[aria-expanded="true"] {
            .b-icon-group-expand {
                &::before {
                    rotate : 90deg;
                }
                &.b-rtl {
                    &::before {
                        rotate : -270deg;
                    }
                }
            }
        }

        .b-cal-event-wrap {
            position       : relative;
            padding-inline : 0;
            /* Margins allow focus outline to be visible */
            margin-inline  : 3px;

            &:first-child {
                margin-block-start : 3px;
            }

            /* Events animate up to height when added */
            &.b-cal-event-bar-adding {
                .b-cal-event {
                    animation : b-anim-grow-height 0.3s ease-out forwards;
                }
            }

            /* Events animate down to zero height before being removed */
            &.b-cal-event-bar-removing {
                .b-cal-event {
                    animation : b-anim-shrink-height 0.3s ease-out forwards;

                }
            }
        }
    }

    /* Textual elements must truncate with ellipsis if too long */
    .b-cal-event-bar-name, .b-cal-event-desc > :first-child {
        overflow      : hidden;
        text-overflow : ellipsis;
    }
}

@keyframes b-anim-grow-height {
    from {
        height  : 0;
        opacity : 0;
    }
    to {
        height  : var(--bi-event-height);
        opacity : 1;
    }
}

@keyframes b-anim-shrink-height {
    0% {
        height  : var(--bi-event-height);
        opacity : 1;
    }
    90% {
        opacity : 0;
    }
    100% {
        height  : 0;
        opacity : 0;
    }
}
