.b-calendar-print-iframe {
    position : absolute;
    top      : -1000em;
    left     : -1000em;
}

.b-calendar {
    &.b-shrinkwrap-row-heights {
        .b-print-content {
            .b-weeks-container {
                flex : 0 0 auto;
            }
        }
    }

    .b-print-content {
        font-family    : 'Helvetica Neue', Arial, Helvetica, sans-serif;
        position       : absolute;
        display        : flex;
        flex-direction : column;
        height         : 100%;
        width          : 100%;

        .b-print-header {
            font-size : 1.4em;
        }

        .b-print-body {
            flex    : 1;
            display : flex;

            .b-print-viewport {

                display : flex;
                flex    : 1;

                header {
                    display : none;
                }

                /* No padding, the views are not contained. */
                .b-calendar-mixin {
                    padding : 0;
                }

                .b-day-view, .b-week-view, .b-month-view {
                    flex : 1 0 100%;

                    .b-month-view-content {
                        padding : 0;
                    }
                }

                .b-day-view, .b-week-view {
                    .b-expand-all-day-button, .b-yscroll-pad {
                        display : none;
                    }

                    .b-day-view-day-content {
                        overflow-y : hidden !important;
                    }
                }

                .b-agenda-view {
                    .b-grid-body-container.b-widget-scroller {
                        overflow-y : hidden !important;
                    }
                }

                .b-year-view {
                    .b-yearv-iew-content {
                        padding    : 0;
                        overflow-y : hidden !important;
                    }

                    /* We lay out 4 across in print mode, so we know we can kill bottom margin */
                    /* to ensure content fits within portrait height. */
                    .b-year-view-month:nth-child(9),
                    .b-year-view-month:nth-child(10),
                    .b-year-view-month:nth-child(11),
                    .b-year-view-month:nth-child(12) {
                        margin-bottom : 0;
                    }
                }
            }
        }
    }
}
