:root, :host {
    /**
     * Time range border width
     */
    --b-calendar-time-range-border-width    : 0.15em;

    /**
     * Time range line size
     */
    --b-calendar-time-range-line-size       : 0.2em;

    /**
     * Time range line zoom scale
     */
    --b-calendar-time-range-line-zoom-scale : 2;
}

.b-colorize {
    /**
     * Time range header background
     */
    --b-calendar-time-range-header-background : var(--b-primary-97);

    /**
     * Time range border color
     */
    --b-calendar-time-range-border-color      : var(--b-primary);
}

.b-internal {
    --bi-time-range-header-width : null;
}

/*
 * If there is no inset before (header) or after (footer), the primary color is applied to the body.
 *
 * Using :where to decrease specificity so that user CSS can override this if needed.
 */
:where(.b-day-view-day-detail:not(.b-day-view-inset-before,.b-day-view-inset-after) .b-cal-time-range-body) {
    /*
     * If there is no inset before (header) or after (footer), and the TimeRange has its own color,
     * that color is applied to the body.
     */
    background-color : var(--b-primary);
}
.b-day-view {
    .b-day-view-day-detail {
        &.b-day-view-inset-before {
            --bi-start-inset : var(--bi-day-view-cell-inset-size);
        }
        &.b-day-view-inset-after {
            --bi-end-inset : var(--bi-day-view-cell-inset-size);
        }
    }
    .b-cal-time-range {
        position       : absolute;
        display        : flex;
        flex-direction : column;
        overflow       : hidden;
        left           : 0;
        right          : 0;

        &.b-cal-time-range-overlay-gutter {
            min-width: calc(100% + var(--bi-day-view-cell-gutter));
        }

        /* Must fit between the two insets unless asked not to */
        &:not(.b-cal-time-range-stretch,.b-cal-time-range-overlay-gutter) {
            .b-cal-time-range-body {
                max-width : calc(100% - calc(var(--bi-start-inset, 0) + var(--bi-end-inset, 0)));
            }
        }

        &.b-cal-time-range-line {
            border-style      : solid;
            transition        : scale 0.3s ease-in-out;
            z-index           : 1;

            border-color      : var(--b-calendar-time-range-border-color);
            border-width      : calc(var(--b-calendar-time-range-border-width)) 0 0 0;
            margin-top        : calc(var(--b-calendar-time-range-line-size) / -2);
            min-height        : var(--b-calendar-time-range-line-size);
            margin-inline-end : calc(-1 * var(--bi-day-view-cell-gutter));

            &.b-cal-time-range-hover-zoom:hover {
                scale : var(--b-calendar-time-range-line-zoom-scale);
            }
        }

        /* Header border */
        &.b-cal-time-range-has-header {
            &::after {
                content          : "";
                position         : absolute;
                top              : 0;
                bottom           : 0;
                opacity          : 0.5;

                /* whichever side the header is on, the line is always at the start */
                inset-inline     : 0;

                background-color : var(--b-calendar-time-range-border-color);
                width            : var(--b-calendar-time-range-border-width);
            }
        }

        /*
         * Time range stretches to fill inset area.
         * If there are headers/footers, then the writing mode is set to vertical-rl or vertical-lr
         * which means that the flexbox *column* layout lays the header/body/footer out horizontally.
         *
         * this may seem counter-intuitive, but in this way all text in all the parts is rotated
         * the same way, making it easier to read.
         *
         * The header and footer use flex : 0 0 var(--bi-day-view-cell-inset-size) to size themselves
         * and the body flexes to fit exactly between them.
         *
         * If there are no extra parts, the writing mode is normal and the body part, being
         * flex : 0 1 100%, fills the time range element vertically.
         */
        .b-cal-time-range-body {
            flex           : 0 1 100%;
            pointer-events : none;
            z-index        : -1; /* ensure body does not cover overlapping header on opposite side inset */
        }

        .b-cal-time-range-header,
        .b-cal-time-range-footer {
            &:not(:empty) {
                flex            : 0 0 var(--bi-day-view-cell-inset-size);
                display         : flex;
                gap             : 0.5em;
                align-items     : center;
                justify-content : center;
            }
        }

        .b-cal-time-range-header {
            transition : background-color 0.1s;
            position   : relative;

            background : var(--b-calendar-time-range-header-background);
            min-width  : var(--bi-time-range-header-width);
        }

        &:not(.b-cal-time-range-narrow) {
            .b-cal-time-range-header-text {
                font-size : 0.9em;
            }
        }
    }

    .b-cal-time-range-rotate-neg {
        align-self   : center;
        writing-mode : vertical-lr;
        rotate       : 180deg;

        .b-cal-time-range-header i::before {
            /* Icon has to be rotated back upright */
            rotate : 180deg;
        }
    }

    .b-cal-time-range-rotate-pos {
        align-self   : center;
        writing-mode : vertical-rl;
    }

    .b-cal-time-range-align-start {
        flex-direction : column-reverse;
    }

}

.b-calendar.b-time-ranges-disabled {
    .b-cal-time-range {
        display: none;
    }
}
