:root, :host {
    /**
     * Border between sidebar and calendar
     */
    --b-sidebar-border-inline-end : null;

    /**
     * The width applied to the `flex-basis` of the Sidebar
     *
     * When there is a date picker, this is overridden to a fixed width
     * to make room for the date picker.
     */
    --b-sidebar-width             : auto;
}

.b-internal {
    --bi-scrollbar-width : null;
}

.b-sidebar {
    flex-shrink                       : 0;

    border-inline-end                 : var(--b-sidebar-border-inline-end);
    flex-basis                        : var(--b-sidebar-width);

    --b-panel-padding                 : 1em;
    --b-toolbar-padding               : 0 0 1em 0;
    --b-toolbar-gap                   : 0.5em;
    --b-date-picker-day-font-size     : 0.85em;
    --b-date-picker-date-font-size    : 0.85em;
    --b-date-picker-toolbar-font-size : .9em;
    --b-date-picker-min-width         : 0;
    --b-calendar-panel-weeks-gap      : 0;
    --b-year-picker-padding           : 0;

    transition                        : flex-basis 0.3s ease;

    .b-calendar-date-picker {
        margin-top        : .5em;

        --b-button-height : auto;
    }

    &.b-has-date-picker {
        --b-sidebar-width : 17.5em;
    }
    &.b-date-picker-with-events {
        --b-sidebar-width : 20em;
    }
    &.b-collapsed, &.b-collapsing {
        --b-sidebar-width    : 0 !important;
        --bi-scrollbar-width : 0 !important;
    }

    /* Prevent the DatePicker from being crushed into a bad UX. */
    /* This is an optimum width in which to lay out the DatePicker. */
    .b-has-date-picker {
        flex-basis : var(--b-sidebar-width);

        /* Vertical overflow must make it expand in width */
        &:has(.b-sidebar-content.b-vertical-overflow) {
            flex-basis : calc(var(--b-sidebar-width) + var(--bi-scrollbar-width));
        }
    }

    [data-ref="resourceFilter"]:not(.b-empty) {
        min-height : 8em;
    }
}

/* If sidebar is being resized by a splitter, turn off transitions so it responds immediately */
.b-calendar:has(.b-sidebar + .b-splitter.b-moving) .b-sidebar {
    transition : none;
}
