/* @after Calendar/view/CalendarEventRendering.css */
:root, :host {
    /**
     * Selected date's background color in the day selector
     */
    --b-event-list-day-selector-selected-date-background : var(--b-calendar-today-background, var(--b-date-picker-today-background, transparent));

    /**
     * Selected date's color in the day selector
     */
    --b-event-list-day-selector-selected-date-color      : var(--b-calendar-today-color, var(--b-date-picker-today-color, var(--b-primary-50)));

    /**
     * Gap between resource avatars
     */
    --b-event-list-event-column-resource-row-gap         : 0.3em;
}

@media print {
    .b-cal-widget-settings-button {
        display : none;
    }
}

.b-event-list {
    .b-grid-panel-body {
        /*  Override Grid's idea about background */
        background : var(--b-calendar-background);
    }

    .b-cal-widget-settings-button {
        position           : absolute;
        font-size          : 110%;
        border-radius      : 50%;
        inset-inline-start : auto;
        inset-inline-end   : 0;
        margin-top         : 1em;
        margin-inline-end  : 1em;
        z-index            : 100;
        width              : 3em;
        height             : 3em;
        box-shadow         : none;
    }

    &:has(.b-grid-panel-body.b-has-scrollbar) {
        .b-cal-widget-settings-button {
            margin-inline-end : 1.75em;
        }
    }

    [data-column="name"] .b-icon-circle::before {
        font-size : var(--b-calendar-event-circle-icon-size);
    }
}

.b-calendar-events-cell {
    padding     : 1em;
    gap         : 1em;
    flex-wrap   : wrap;
    align-items : flex-start;

    .b-cal-event-resource-avatars {
        flex-basis     : 30%;
        flex-direction : column;

        min-width      : var(--b-avatar-size);
        gap            : var(--b-event-list-event-column-resource-row-gap);
    }
}

.b-cal-event-resource-avatar-row {
    display     : flex;
    flex-flow   : row nowrap;
    gap         : 1em;
    align-items : center;
}

.b-cal-event-resource-avatar-desc {
    display   : flex;
    flex-flow : column;
    gap       : 0.3em;
}

.b-cal-event-column-event-desc {
    margin-inline-start : auto;
    display             : flex;
    flex-direction      : column !important;
    flex-flow           : row nowrap;
    justify-content     : center;

    height              : var(--b-avatar-size);
}

.b-cal-event-column-times {
    display        : flex;
    flex-direction : column;
}

.b-event-column-resource-role {
    font-size   : 80%;
    font-weight : 300;
}

.b-cal-event-list-event-time {
    display     : flex;
    align-items : center;

    min-height  : var(--b-avatar-size);
}

.b-cal-event-column-event-name {
    font-weight : bold;
}
