/* @after Calendar/widget/CalendarEventRendering.css */
/**
 * Note that MonthAgenda is a view designed specifically for use on phones
 */
.b-month-agenda-view {
    --b-agenda-view-cell-padding : 0;
    --b-date-picker-min-width    : 0;

    .b-calendar-day-header {
        padding-block: 0.7em 0.5em;
    }
    .b-calendar-week .b-calendar-cell .b-date-picker-cell-inner {
        margin-bottom : 0.2em;
    }
    .b-weeks-container {
        z-index       : 1;

        border-bottom : 1px solid var(--b-calendar-border-color);
    }
    .b-month-agenda-agenda {
        &[data-duration="1"] {
            /* Hide date headers for all possible child types. */
            /* We accept AgendaView, DayView and DayAgendaView. */
            .b-cal-agenda-date, .b-calendar-row-header {
                display : none;
            }
            /* If it's a single day agenda (the default), then */
            /* the one row gets no special hover rendition. */
            .b-grid-row {
                border     : 0 none;
                min-height : 100%;
                &.b-hover, .b-grid-cell:not(:focus-visible) {
                    background-color : inherit !important;
                }
            }
        }
        /* Multi-day, the the dates need to be shown, but smaller. */
        &:not([data-duration="1"]) {
            .b-cal-agenda-date-date-number {
                font-size : 2em;
            }
        }
    }
}

@media (max-height : 600px) {
    .b-month-agenda-view {
        .b-weeks-container {
            font-size : 85%;
        }
        .b-calendar-week .b-calendar-cell .b-date-picker-cell-inner {
            margin : 0;
        }
        .b-panel-header {
            padding-block : 0.5em;
        }
        .b-cal-agenda-event-row {
            margin-bottom : calc(var(--bi-event-row-spacing) / 2);
        }
    }
}

@media (max-width : 450px) {
    .b-month-agenda-view .b-agenda-view .b-cal-event-bar-container {
        margin-inline-start : 1em;
    }
}
