/* @after Calendar/widget/DayView.css */

:root, :host {
    /**
     * Hour hover background
     * @state Hovered
     */
    --b-day-agenda-view-hour-hover-background : var(--b-neutral-95);
}

.b-internal {
    --bi-gutter-width    : null;
    --bi-min-event-width : null;
}

.b-day-agenda-view {
    --bi-hour-cell-padding-inline-start : 0.5em;
    --bi-hour-event-padding-top         : 0.5em;
    --bi-hour-border-width              : 1px;
    --bi-hour-border-color              : var(--b-day-view-hour-line-color);
    --bi-event-flow-direction           : column;
    --bi-event-spacing                  : 5px;
    --bi-hour-width                     : var(--bi-time-axis-width);

    .b-day-view-day-container {
        background-image : none;
        /* TBD, should columns stretch? */
        /* align-items: baseline; */
        clip-path        : none;
    }

    .b-day-agenda-hour-time {
        font-size         : 70%;
        display           : flex;
        justify-content   : center;

        border-inline-end : var(--bi-hour-border-width) solid var(--bi-hour-border-color);
    }

    .b-day-agenda-hour-time, .b-day-agenda-hour-events {
        padding-top    : var(--bi-hour-event-padding-top);
        padding-bottom : var(--bi-event-spacing);
    }

    .b-day-view-day-detail.b-calendar-cell {
        display        : flex;
        flex-direction : column;
        /* gap            : var(--bi-hour-spacing); */
        height         : auto;

        .b-day-agenda-hour {
            display               : grid;
            position              : relative;

            grid-template-columns : var(--bi-hour-width) 1fr;
            border-bottom         : var(--bi-hour-border-width) solid var(--bi-hour-border-color);
            flex                  : 0 0 var(--bi-hour-height);

            &.b-hover-hour::before {
                content    : "";
                position   : absolute;
                height     : 100%;
                width      : 100%;
                z-index    : -1;

                background : var(--b-day-agenda-view-hour-hover-background);
            }
        }

        .b-day-agenda-hour-events {
            display        : flex;
            overflow       : hidden;

            flex-direction : var(--bi-event-flow-direction);
            gap            : var(--bi-event-spacing);
            padding-inline : var(--bi-hour-cell-padding-inline-start) var(--bi-gutter-width);

            .b-cal-event-wrap {
                position : relative;
            }

            /* Column direction must stretch the height, not wrap to new columns.  */
            /* Horizontally layed out events must wrap; */
            .b-event-flow-row & {
                flex-wrap : wrap;
            }
        }

        .b-cal-event-body {
            padding : 0.5em;
        }

        /* Move the drag event to where the event cells are */
        .b-cal-drag-proxy {
            opacity : 1;
        }

        &:has(.b-cal-drag-proxy) {
            z-index : 1;

            .b-cal-event {
                background-image : none;
                padding          : 0
            }
        }
    }

    &.b-multi-day-view {
        &:is(.b-hide-time-axis,:not(.b-synced-hour-heights)) {
            .b-day-view-all-day-row-start {
                display : none;
            }
        }
        /* The individual day columns have their own time axis. */
        /* We need to create a full height border for each column's time axis */
        &:not(.b-synced-hour-heights, .b-hide-time-axis) {
            .b-day-view-day-detail.b-calendar-cell::before {
                content            : "";
                position           : absolute;
                top                : 0;
                width              : 1px;
                height             : 100%;

                inset-inline-start : calc(var(--bi-time-axis-width) - 1px);
                background-color   : var(--b-day-view-hour-line-color);
            }
        }
    }

    &:not(.b-multi-day-view) {
        &.b-hide-time-axis {
            .b-day-view-all-day-row-start {
                display : none;
            }
        }
        /* There is no time axis column in a single day view. */
        /* Each hour has its own hour cell so we need to create */
        /* a full height border to the event area. */
        &:not(.b-hide-time-axis) {
            .b-day-view-day-detail.b-calendar-cell::before {
                content            : "";
                position           : absolute;
                top                : 0;
                width              : 1px;
                height             : 100%;

                inset-inline-start : calc(var(--bi-time-axis-width) - 1px);
                background-color   : var(--b-day-view-hour-line-color);
            }
            .b-day-view-day-container {
                border-inline-start-color : transparent;
            }
        }
    }

    &.b-event-flow-row {
        .b-day-view-day-detail.b-calendar-cell {
            .b-day-agenda-hour-events {
                flex-wrap : wrap;

                .b-cal-event-wrap {
                    flex      : 1;
                    width     : auto;

                    min-width : clamp(var(--bi-min-event-width), 100%)
                }
            }
        }
    }

    &.b-hide-borders {
        --bi-hour-border-width : 0;

        .b-calendar-cell {
            .b-day-agenda-hour-events {
                padding-bottom : 0;
            }
        }

        /*  No last spacing if no border */
        .b-day-view-day-detail.b-calendar-cell {
            .b-day-agenda-hour-events {
                .b-cal-event-wrap {
                    margin-bottom : 0;
                }
            }
        }

        .b-day-agenda-hour.b-hover-hour::before {
            inset-block : calc(var(--bi-hour-event-padding-top) / 2);
        }
    }

    /* Normally, no outer time axis is shown because each day column has its own.  */
    /* When hour heights are synced and timeaxis not hidden, this is displayed.  */
    .b-time-axis-container {
        display          : none;
        height           : fit-content;
        background-image : none;

        .b-day-agenda-hour {
            display       : grid;

            border-bottom : var(--bi-hour-border-width) solid var(--bi-hour-border-color);
            flex          : 0 0 var(--bi-hour-height);
        }

        .b-day-agenda-hour-events {
            display : none;
        }
    }

    &.b-hide-time-axis, &.b-multi-day-view.b-synced-hour-heights {
        /* An outer time axis is shown when hour heights are synced  */
        &:not(.b-hide-time-axis) {
            .b-time-axis-container, .b-day-view-all-day-row-start {
                display : flex;
            }
        }

        .b-day-view-day-detail.b-calendar-cell {
            .b-day-agenda-hour-time {
                display : none;
            }

            .b-day-agenda-hour {
                grid-template-columns : 1fr;
            }

            .b-cal-tentative-event {
                margin-left : 0;
            }
        }
    }

    /* No visible left border for calendar modes */
    .b-day-view-day-container, .b-day-view-schedule-container {
        border-inline-start : 0 none;
    }

    &.b-hide-empty-hours {
        .b-day-agenda-hour:not(.b-has-events) {
            display : none;
        }
    }
}
