.b-calendar.b-outer {
    height : 100%;
}

.b-calendar, .b-calendar-content {
    /* The row items are fitted */
    overflow : hidden;
}

.b-calendar {
    user-select         : none;
    -webkit-user-select : none;

    :is([role="gridcell"],[role="columnheader"]):focus-visible {
        outline          : none;

        /* Important rule because focused state takes precedence over all other states */
        background-color : var(--b-calendar-focused-cell-background-color, var(--b-primary-95)) !important;
    }

    &.b-overflow-popup-visible {
        /* Boost the Calendar's stacking position while an overflow popup is visible */
        /* so that if it overflows the bounds of the Calendar element, it sits above */
        /* surrounding static elements. */
        z-index : var(--b-calendar-overflow-popup-visible-z-index);
    }

    .b-calendar-content {
        /* Not using var, that would change all nested panels too */
        gap : 0;
    }

    /* Nesting required to win over other bundle themes */
    /* https://github.com/bryntum/support/issues/2820 */
    .b-calendar-view-desc-text {
        overflow       : hidden;
        white-space    : nowrap;
        text-overflow  : ellipsis;
        text-transform : capitalize;

        color          : var(--b-calendar-view-desc-color);
        font-size      : var(--b-calendar-view-desc-font-size);
        font-weight    : var(--b-calendar-view-desc-font-weight);
    }

    .b-calendar-toolbar {
        background    : transparent;

        border-bottom : var(--b-calendar-toolbar-border-bottom);

        /* Prev and next buttons touch when adjacent */
        [data-ref="prevButton"] + [data-ref="nextButton"] {
            margin-inline-start : calc(var(--b-toolbar-gap) * -1);
        }
    }

    &:not(.b-calendar-nav-toolbar) .b-calendar-toolbar {
        .b-cal-nav-item {
            display : none;
        }
    }

    &:not(.b-calendar-nav-sidebar) .b-sidebar {
        .b-cal-nav-item {
            display : none;
        }
    }

    &.b-responsive-small {
        /* Desc margin-start is only needed in responsive-small when gap is unset */
        .b-calendar-view-desc {
            margin-inline-start : 0.5em;
        }

        .b-calendar-toolbar {
            --b-toolbar-min-size : 2em;
            --b-toolbar-padding  : 0.2em;

            .b-calendar-view-desc-text {
                font-size : 120%;
            }

            > .b-toolbar-content {
                gap : unset;
            }

            button {
                --b-button-padding-inline : 1em;
                aspect-ratio              : unset;

                &:not(.b-pressed) {
                    background-color : transparent;
                    border-color     : transparent;
                }
            }

            .b-sidebar-toggle {
                margin-inline-end : 0.5em;
            }

            .b-calendar-mode-button,
            .b-calendar-today-button {
                > .b-button-menu-icon,
                > label {
                    display : none;
                }
            }
        }
    }

    &:not(.b-responsive-small) {
        .b-calendar-toolbar {
            .b-calendar-mode-button {
                > .b-button-icon {
                    display : none;
                }
            }
        }
    }

    .b-calendar-view-container {
        /* More space at top when used in a Calendar */
        > .b-month-view,
        > .b-year-view {
            > .b-panel-body-wrap > .b-panel-content {
                padding-top : var(--b-calendar-view-padding-top);
            }
        }
    }
}

.b-notransition:is(.b-calendar-mixin, .b-calendar) * {
    transition : none !important;
}

.b-panel-ui-calendar-banner.b-panel-header {
    --b-panel-header-padding       : 2em 1em;
    --b-panel-header-border-bottom : var(--b-calendar-toolbar-border-bottom);
    --b-panel-header-color         : var(--b-calendar-view-desc-color);
    --b-panel-header-font-size     : var(--b-calendar-view-desc-font-size);
    --b-panel-header-font-weight   : var(--b-calendar-view-desc-font-weight);
    --b-panel-header-background    : var(--b-grid-toolbar-background);

    /* TODO : Use var to match */
    height                         : 42px;
}
