/* @after Calendar/view/CalendarEventRendering.css */
:root, :host {
    /**
     * Resource header height
     */
    --b-resource-view-header-height : 4.5em;
}

.b-internal {
    --bi-view-gap       : null;
    --bi-resource-width : null;
}

.b-resource-view {
    align-items                   : stretch;

    --b-panel-gap                 : 0;
    --b-panel-header-font-size    : 1em;
    --b-panel-with-header-padding : 0;

    .b-cal-widget-settings-button {
        display : none;
    }

    /* If we contain any DayView with an overflowing all day row, we need to show the all day expand button */
    &:has(.b-day-view:not(.b-resource-day-view-time-axis) .b-day-view-all-day-row-start.b-has-cell-overflow:not(.b-day-view-all-day-auto-height)) {
        .b-resource-day-view-time-axis {
            cursor : pointer;

            .b-expand-all-day-button {
                display : flex;
            }
        }
    }

    /* If we contain any DayView with an expanded all day row, we need to rotate the expand button */
    &:has(.b-day-view:not(.b-resource-day-view-time-axis) .b-day-view-all-day-row-start.b-expanded) {
        .b-expand-all-day-button {
            rotate : -180deg;
        }
    }
}

.b-resource-view-content {
    flex-flow   : row nowrap;
    align-items : stretch;
    padding     : 0;

    > * {
        flex              : 1 0 0;
        margin-inline-end : var(--bi-view-gap);

        &.b-resource-view-resource {
            min-width : var(--bi-resource-width);
        }

        &.b-filtered-hiding {
            min-width  : 0;
            width      : 0;
            flex       : none;
            border     : 0 none;
            transition : width 0.3s;
        }

        &:last-child,
        &.b-last-resource-view {
            margin-inline-end : 0;
        }
    }

    .b-panel-header {
        background    : var(--b-day-view-background);
        border        : 1px solid var(--b-day-view-border-color);
        border-block  : none;
        border-radius : 0;
        color         : var(--b-month-view-cell-color);
        height        : var(--b-resource-view-header-height);
    }

    .b-day-view.b-first-resource-view > .b-panel-header {
        border-inline-start : none;
    }

    > .b-day-view {
        /* Both time axes, the day time axis and the scroller are sticky at either end */
        &.b-resource-day-view-time-axis {
            position : sticky;
            z-index  : 3;

            /* When it is the time axis (first child), it's docked at the start and has *no day container* */
            &:first-child {
                inset-inline-start : 0;
                border-inline-end  : 1px solid var(--b-day-view-border-color);

                .b-day-view-day-container {
                    display : none;
                }
            }

            .b-virtual-scrollers {
                display : none;
            }
        }

        /* Time axis has a border-right, so the first day-view needs no border-inline-starts */
        &.b-first-resource-view {
            .b-day-view-schedule-container, .b-day-view-day-container {
                border-inline-start : 0 none;
            }
        }
        &.b-last-resource-view {
            .b-panel-header {
                border-inline-end : 0 none;
            }
        }

        &.b-resource-day-view-scroller {
            /* Scroller is docked at the end */
            inset-inline-end    : 0;

            background          : var(--b-day-view-background) !important;
            z-index             : 1;

            .b-day-view-time-axis-time, .b-day-view-all-day-row-start {
                display : none;
            }

            .b-day-view-day-container {
                visibility : hidden;
            }

            .b-time-axis-container {
                display : none;
            }

            border-inline-start : var(--b-day-view-split-width) solid var(--b-day-view-border-color);

            .b-yscroll-pad {
                visibility : hidden;
            }
        }

        &:not(.b-resource-day-view-time-axis, .b-resource-day-view-scroller) {
            &:not(.b-last-resource-view) {
                .b-day-view-day-container, .b-day-view-schedule-container {
                    border-inline-end : var(--b-day-view-split-width) solid var(--b-day-view-border-color);
                }
            }

            .b-day-view-all-day-row-start, .b-time-axis-container {
                display : none;
            }
        }

        &.b-resource-day-view-time-axis,
        &.b-resource-day-view-scroller {
            flex              : 0 0 auto;
            margin-inline-end : 0;

            /* The time axis and the scrollbar must hide their header */
            .b-day-view-header {
                visibility     : hidden;
                padding-inline : 0;
            }

            /* This must not be display : none because it provides height */
            .b-day-view-schedule-container {
                display   : flex;
                max-width : 0;
                border    : 0 none;
            }
        }
    }

    .b-day-view-day-container {
        overscroll-behavior : auto
    }

    .b-month-view-content {
        padding-top : 0;

        .b-calendar-weekdays {
            padding       : 0.5em 0;
            border-inline : 1px solid var(--b-day-view-border-color);

            .b-calendar-day-header {
                padding-bottom : 0;
            }
        }
    }

    /* Covers both EventList and AgendaView */
    > .b-event-list > .b-panel-body-wrap {
        border-inline : 1px solid var(--b-day-view-border-color);
    }
}

.b-calendar .b-resource-view-title {
    display         : grid;
    column-gap      : 1em;
    row-gap         : 0;
    align-items     : center;
    justify-content : flex-start;

    .b-resource-avatar {
        font-size : 1.2em;
    }

    &.b-has-meta .b-resource-avatar {
        grid-row : 1 / span 2;
    }

    /* .b-resource-avatar { */
    /*     border : 1px solid currentColor; */
    /* } */

    .b-resource-name,
    .b-resource-meta {
        grid-column : 2;
    }

    .b-resource-name {
        font-size : 1.1em;
    }

    .b-resource-meta {
        font-size   : .7em;
        font-weight : 400;
    }
}
